import React, {useState} from 'react'
import ContactsModal from "./contactsModal";
import classes from "../../../styles.module.scss";

const ProfileData = ({profile}) => {

    const [showWindow, setShowWindow] = useState( false )

    const toggleSwitchingContactsWindow = () => {
        !showWindow ?
            setShowWindow(true)
            : setShowWindow(false)
    }

    return (
        <div className={classes.descriptionBlock}>
            {
                profile.lookingForAJob
                    ? <div className={classes.jobLooker}>
                        <strong>User is looking for a job!</strong>
                        <div>
                            <b>Description:</b> <br/> <p>{profile.lookingForAJobDescription}</p>
                        </div>
                    </div>
                    : <div className={classes.jobLooker}>
                        <strong>User is not looking for a job!</strong>
                        <div>
                            <strong>Description:</strong> <br/> <p>{profile.lookingForAJobDescription}</p>
                        </div>
                    </div>
            }

            <div className={classes.aboutMe}>
                {
                    <div> <strong>About me: </strong> <br/> <p>{profile.aboutMe}</p></div> || <b>About Me</b>
                }
            </div>

            <div>
                <button onClick={toggleSwitchingContactsWindow} className={classes.showContactsButton}>Contacts...</button>
            </div>

            {  showWindow ?
                <div className={classes.contactsWindow} >
                <ContactsModal profile={profile}/>
                </div>
                : null
            }

        </div>
    )
}

export default ProfileData






