import React from 'react'
import {Field, reduxForm} from "redux-form";
import {createField, Input} from "../../common/FormsControls/FormsControls";
import {required} from "../../../utils/validators/validators";
import classes from '../../../styles.module.scss'

const LoginForm = ({handleSubmit, error, captcha}) => {
    return <div className={classes.loginWrapper}>
        <div className={classes.loginContainer}>
        <h1 className={classes.loginTitle}>Login</h1>
        <form onSubmit={handleSubmit}>
            <div className={classes.loginFormField}>
                <Field placeholder={'Login'} name={'email'}
                       validate={[required]}
                       component={Input}/>
            </div>
            <div className={classes.loginFormField}>
                <Field placeholder={'Password'} name={'password'}
                       type={'password'}
                       validate={[required]}
                       component={Input}/>
            </div>
            <div className={classes.loginFormField}>
                <div className={classes.rememberMeWrapper}>
                <label className={classes.rememberMe}>
                    <Field type={'checkbox'} name={'rememberMe'} component={Input}/>
                </label>
                <span className={classes.rememberMeSpan}>remember me</span>
                </div>
            </div>
            { captcha && <img src={captcha} /> }
            { captcha && createField('Enter captcha', 'captcha', [required], Input, {}) }
            {error && <div className={classes.formSummaryError}>
                <strong>{error}</strong>
            </div>}
            <div className={classes.loginFormField}>
                <button type={'submit'}>Log In</button>
            </div>
        </form>
        </div>
    </div>
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

export default LoginReduxForm