import React, {useState} from 'react'
import Loader from "../../common/Loader/Loader";
import userAvatar from '../../../assets/images/avatar-batman-small.png'
import ProfileStatusWithHooks from "../ProfileStatus/ProfileStatusWithHooks";
import ProfileData from "./ProfileData";
import ProfileDataForm from "./ProfileDataForm";
import {Transition} from "react-transition-group";
import classes from '../../../styles.module.scss'
import {login} from "../../../redux/auth-reducer";


const ProfileInfo = ({profile, saveProfile, ...props}) => {

    const [changeAvatarMode, setChangeAvatarMode] = useState(false)
    const [infoEditMode, setInfoEditMode] = useState(false)

    function onToggleEditMode() {
        !infoEditMode ?
            setInfoEditMode(true)
            :
            setInfoEditMode(false)
    }


    function onToggleChangeAvatarMode() {
        !changeAvatarMode ?
            setChangeAvatarMode(true)
            :
            setChangeAvatarMode(false)
    }

    if (!profile) {
        return <Loader/>
    }

    const onMainPhotoSelected = e => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    const onSubmit = formData => {
        saveProfile(formData).then(
            () => {
                setInfoEditMode(false)
            }
        )
    }

    return (
        <div className={classes.profilePage}>
            <div className={classes.userPageHeader}>
                <img src={profile.photos.large || userAvatar} className={classes.userAvatar}
                     alt={'Аватар пользователя'} onClick={() => setChangeAvatarMode(!changeAvatarMode)}/>
                <div className={classes.nameAndStatus}>
                    <strong className={classes.userName}>{profile.fullName}</strong>
                    <ProfileStatusWithHooks status={props.status}
                                            updateStatus={props.updateStatus}
                                            userId={props.userId}
                                            profile={profile}
                                            authorizedUserId={props.authorizedUserId}/>

                    {props.isOwner && <button onClick={onToggleEditMode} className={classes.infoEditButton}>Edit Info</button>}
                </div>
            </div>
            {/*{!changeAvatarMode ||*/}
            <div className={classes.wrapper}>
                <Transition
                    in={changeAvatarMode}
                    timeout={1000}
                    mountOnEnter
                    unmountOnExit
                >{state => <div className={`${photoInputClass}  ${state}`}>
                    {console.log(state)}
                    {props.isOwner && <label htmlFor={'file'}> Choose a file
                        <input type={'file'} onChange={onMainPhotoSelected} name={'file'} id={'file'}
                               className={classes.inputFile}/>
                    </label>
                    }
                </div>
                }
                </Transition>
            </div>
            {/*}*/}
            {!infoEditMode ?
                <ProfileData profile={profile}/>
                : <ProfileDataForm profile={profile} onSubmit={onSubmit} initialValues={profile} />
            }
        </div>
    )
}

const photoInputClass = classes.photoInput

export default ProfileInfo